import { StarRating } from './StarRating.js'

import './color.scss'

export const Color = ({ color, title, starLimit, starSelected, onRate }) =>
  <div className="color-container">
    <div className="color-top" style={ color } />
    <div className="color-description">
      <p>{ title }</p>
      <StarRating starLimit={starLimit}
        starSelected={starSelected}
        onRate={onRate} />
    </div>
  </div>



